import React, { Component } from 'react';
import Modal from 'react-awesome-modal';
import Con_list from './todo/Con_list';
import './Con_wl.css';

class Contract_wlist extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false
        };
    }

    openModal() {
        this.setState({
            visible: true
        });
    }

    closeModal() {
        this.setState({
            visible: false
        });
    }

    state = {
        employerName: '',
        employeeName: '',
        contractTerms: '',
        contractTerme: '',
        workPlace: '',
        workDetail: '',
        workTime: '',
        wage: '',
        payDay: '',
    }
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }
    render() {
        return (
            <div className="con_wlist clear">
                <div className="con_w">
                    <div className="title_name"><span>계약서 작성</span></div>
                    <div id="con3_1" className="clear">
                        <form id="form" method="get">
                            <div className="employerName">
                                <input type="text" name="employerName" value={this.state.name} onChange={this.handleChange} placeholder="ex) 사장님" />
                            </div>
                            <div className="employeeName">
                                <input type="text" name="employeeName" value={this.state.name} onChange={this.handleChange} placeholder="ex) 도비" />
                            </div>
                            <div className="contractTerms">
                                <input type="date" name="contractTerms" value={this.state.name} onChange={this.handleChange} />
                            </div>
                            <div className="contractTerme">
                                <input type="date" name="contractTerme" value={this.state.name} onChange={this.handleChange} />
                            </div>
                            <div className="workPlace">
                                <input type="text" name="workPlace" value={this.state.name} onChange={this.handleChange} placeholder="ex) ***편의점" />
                            </div>
                            <div className="workDetail">
                                <input type="text" name="workDetail" value={this.state.name} onChange={this.handleChange} placeholder="ex) 청소" />
                            </div>
                            <div className="workTime">
                                <input type="text" name="workTime" value={this.state.name} onChange={this.handleChange} placeholder="ex) 7시간" />
                            </div>
                            <div className="wage">
                                <input type="number" name="wage" value={this.state.name} onChange={this.handleChange} min="8350" placeholder="8350" />
                            </div>
                            <div className="payDay">
                                <input type="text" name="payDay" value={this.state.name} onChange={this.handleChange} placeholder="ex) 15일" />
                            </div>
                            <input className="preview" type="button" value="미리보기" onClick={() => this.openModal()} />
                            <input className="covenant" type="submit" value="계약하기" />
                        </form>
                        <div className="view">
                            <Modal visible={this.state.visible} width="70%" height="70%" effect="fadeInUp" onClickAway={() => this.closeModal()}>
                                <p id="employerName" >{this.state.employerName}</p>
                                <p id="employeeName">{this.state.employeeName}</p>
                                <p id="contractTerms">{this.state.contractTerms}</p>
                                <p id="contractTerme">{this.state.contractTerme}</p>
                                <p id="workPlace">{this.state.workPlace}</p>
                                <p id="workDetail">{this.state.workDetail}</p>
                                <p id="workTime">{this.state.workTime}</p>
                                <p id="wage">{this.state.wage}</p>
                                <p id="payDay">{this.state.payDay}</p>
                                <button className="pre_bt" onClick={() => this.closeModal()}>Close</button>
                            </Modal>
                        </div>
                    </div>
                </div>
                <div className="all_con_list">
                    <div className="title_name">
                        <span>계약서 목록</span>
                    </div>
                    <div className="con_list">
                        <Con_list/>
                        <Con_list />
                        <Con_list />
                        <Con_list />
                    </div>
                </div>
            </div>
        );
    }
}

export default Contract_wlist;