import React from 'react';
import { BarChart } from 'react-easy-chart';

const Graph = () => {
    return (
        <div>
            <h2>
                Graph
            </h2>
                <BarChart
                    axisLabels={{ x:'바꾸면',y: '나와야지' }}
                    axes
                    colorBars
                    height={250}
                    width={400}
                    barWidth={20}
                    xType={'time'}
                    tickTimeDisplayFormat={'%a'}
                    xDomainRange={['1-Jan-15', '20-Jan-15']}
                    data={[
                        { x: '10-Jan-15', y: 20 },
                        { x: '12-Jan-15', y: 10 },
                        { x: '15-Jan-15', y: 33 }
                    ]}
                />
                <BarChart
                    axisLabels={{ x: '바보', y: '종훈' }}
                    axes
                    grid
                    colorBars
                    height={250}
                    width={400}
                    data={[
                        { x: 'A', y: 46 },
                        { x: 'B', y: 11 },
                        { x: 'C', y: 12 },
                        { x: 'D', y: 1 },
                        { x: 'E', y: 42 },
                        { x: 'F', y: 32 },
                        { x: 'G', y: 27 },
                    ]}
                />
        </div>
    );
}

export default Graph;