import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Modal from "react-awesome-modal";
import "./css/Nav.css";
import Logo from "../img/Images/icLoginLogo.png";

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false
    };
  }

  openModal() {
    this.setState({
      visible: true
    });
  }

  closeModal() {
    this.setState({
      visible: false
    });
  }
  render() {
    return (
      <div className="nav clear">
        <NavLink to="/">
          <div className="logo clear">
            <img src={Logo} alt="logo"/>
          </div>
        </NavLink>
        <div className="nav_list clear">
          <ul className="clear">
            <li>
              <NavLink exact to="/Contract_wlist" activeClassName="active">
                단기 근로계약서
              </NavLink>
            </li>
            <li>
              <NavLink exact to="/Graph" activeClassName="active">
                그래프
              </NavLink>
            </li>
            <li>
              <NavLink to="/Alba" activeClassName="active">
                알바 관리
              </NavLink>
            </li>
          </ul>
        </div>
        <div className="lg_cash clear">
          <ul>
            <li>캐쉬</li>
            <li onClick={() => this.openModal()}>로그인</li>
            <div className="lg_view">
              <Modal className="modal" visible={this.state.visible} width="380px" height="500px" effect="fadeInUp" onClickAway={() => this.closeModal()}>
                <div className="logo_img">
                  <div className="img" />
                </div>
                <div className="lrView line">
                  <div>USER ID</div>
                  <input className="leftItem" type="text" />
                  <div>PASSWORD</div>
                  <input className="leftItem" type="password" />
                  <button className="leftItem lg_bt le_bu">Log in</button>
                </div>
                <div className="lrView">
                  <button className="rigthItem lg_bt">Sign up</button>
                  <button className="rigthItem lg_bt">gmail로 로그인 하기</button>
                  <button className="rigthItem lg_bt" onClick={() => this.closeModal()}>
                    홈으로
                  </button>
                </div>
              </Modal>
            </div>
          </ul>
        </div>
      </div>
    );
  }
}

export default Nav;
