import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Contract_wlist, Graph, Alba, Main } from 'pages';
import { Nav } from 'components';

class App extends Component {
    render() {
        return (
            <div>
                <div className="header">
                    <Nav/>
                </div>
                <div className="section" style={{ maxWidth: '1000px', margin: '0 auto', backgroundColor:'#f8f9fa'}}>
                    <Switch>
                        <Route exact path="/" component={Main} />
                        <Route path="/Contract_wlist" component={Contract_wlist} />
                        <Route path="/Graph" component={Graph} />
                        <Route path="/Alba" component={Alba} />
                    </Switch>
                </div>
                <div className="footer"></div>
            </div>
        );
    };
}

export default App;