import React from 'react';
import './css/Mainimg.css';
import main_4 from '../img/main_4.jpg';
import main_2 from'../img/main_2.jpg';
import main_3 from'../img/main_3.jpg';

const Main = () => {
    return (
        <div className="main">
            <div className="main_img">
                <img className="mainimg_3" src={main_3} alt="사진" />
            </div>
            <div className="main_img">
                <img className="mainimg_4" src={main_4} alt="사진" />
            </div>
            <div className="main_img" >
                <img className="mainimg_2" src={main_2} alt="사진" />
            </div>            
        </div>
    );
};

export default Main;